import React, {useEffect, useState} from "react";
import { Row } from 'react-bootstrap';
import Ticker from 'react-ticker'

const TickerComponent = (props) => {

    const [message, setMessage] = useState();
    const [isLoaded, setIsLoaded] = useState(false);
   
    useEffect(() => {

        // only fires whenever requestConnectionToWallet is updated.
        async function initTicker() {
            const data = await (await fetch("/data/ticker.json")).json()
            setMessage(data.map( d => `$ ${d.token} : $${d.price} | `));
            setIsLoaded(true);
        }

        initTicker()
      }, [])
    
    return (
        <Row>
            {isLoaded &&
                <Ticker offset="run-in" mode="await" speed={3}>
                    {({ index }) => (
                        <div style={{width: 500 + 'px'}}>{message}</div>
                    )}
                </Ticker>
            }
        </Row>   
    )

}

export default TickerComponent;